import React, {useEffect, useState} from 'react';
import {Card, CardHeader, Divider, CardContent, Grid, Button} from '@material-ui/core';
import {gridSpacing} from '../../store/constant';
import {Container, ListGroup} from 'react-bootstrap';
import SubCard from '../../ui-component/cards/SubCard';
import axios from 'axios';

export const ConfirmDeleteModal = (props) => {
    const BASE_URL = 'https://backend-test.alsahaba.sa';
    const [isLoading, setIsLoading] = useState(false);

    const handleDelete = async () => {
        await axios({
            method: 'DELETE',
            url: `${BASE_URL}/user/${props?.patient.id}/delete`
        })
            .then(function (response) {
                window.location.reload();
            })
            .catch(function (response) {
                alert('An error occured!');
            });
    };
    return (
        <>
            <Container>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Card className="popup-container">
                            <CardHeader title="Consultation" />
                            <Divider />
                            <CardContent className="popup-body" style={{top: '250px', overflowY: 'auto', height: '75vh'}}>
                                <Button id="closeBtn" onClick={props?.handleClose}>
                                    X
                                </Button>
                                <Grid container spacing={gridSpacing}>
                                    <Grid item xs={12} sm={6}>
                                        <SubCard title={props?.patient.full_name}>
                                            <h2>Are you sure you want to delete this {props?.patient.is_doctor ? 'doctor?' : 'patient?'}</h2>
                                            <Button style={{color: 'red'}} onClick={handleDelete}>
                                                Yes
                                            </Button>
                                            <Button style={{color: 'green'}} onClick={props?.handleClose}>
                                                No
                                            </Button>
                                        </SubCard>
                                    </Grid>
                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </Container>
        </>
    );
};
