import React from 'react';
import {useEffect} from 'react';

import {makeStyles} from '@material-ui/core/styles';

import {Card, CardHeader, Divider, CardContent, Grid, Button, OutlinedInput, InputAdornment} from '@material-ui/core';

import {gridSpacing} from '../../store/constant';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import {IconSearch} from '@tabler/icons';

import axios from 'axios';

import {ShowSelectedPatient} from './ShowSelectedPatient';
import {ConfirmDeleteModal} from './ConfirmDeleteModal';

const useStyles = makeStyles({
    table: {
        minWidth: 350
    },
    searchControl: {
        width: '100%',
        paddingRight: '8px',
        paddingLeft: '16px',
        marginBottom: '16px',
        marginTop: '16px'
    }
});

export default function Patients() {
    const classes = useStyles();
    const BASE_URL = 'https://backend-test.alsahaba.sa';
    const [patients, setPatients] = React.useState([]);
    const [selectedPatient, setSelectedPatient] = React.useState([]);
    const [value, setValue] = React.useState('');
    const [visible, setVisibility] = React.useState(false);
    const [showDeleteModal, setShowDeleteModal] = React.useState(false);
    const [fileTypes, setFileTypes] = React.useState({});

    const fetchPatients = async () => {
        await axios.get(`${BASE_URL}/users/`).then((response) => {
            setPatients(response.data);
        });
    };
    const fetchSearch = async () => {
        await axios.get(`${BASE_URL}/search?query=${value}`).then((response) => {
            setPatients(response.data);
        });
    };
    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    const closeDeleteModal = () => {
        setShowDeleteModal(false);
    };
    const showDeletePopUpModal = (patient) => {
        setShowDeleteModal(true);
        setSelectedPatient(patient);
    };
    const closePopup = () => {
        setVisibility(false);
    };
    const showSelectedPatient = (patient) => {
        setVisibility(true);
        setSelectedPatient(patient);
    };
    const handleChange = (event) => {
        if (event.target.value === '') {
            setValue(event.target.value);
            fetchPatients();
        } else {
            setValue(event.target.value);
            fetchSearch();
        }
    };
    useEffect(() => {
        const checkFileType = async (patient) => {
            try {
                return new Promise((resolve, reject) => {
                    const img = new Image();
                    img.onload = function () {
                        // If the image is successfully loaded, it's an image
                        resolve(true);
                    };
                    img.onerror = function () {
                        // If an error occurs during loading, it's not an image
                        resolve(false);
                    };
                });
            } catch (error) {
                console.error(`Error checking file type for patient ${patient.id}:`, error);
                return false;
            }
        };

        const checkImageType = async () => {
            patients
                ?.filter((user) => !user.is_doctor)
                .forEach(async (patient) => {
                    if (!fileTypes[patient.id]) {
                        const isImage = await checkFileType(patient);
                        // Update the fileTypes state with the patient ID and its content type
                        if (isImage) {
                            setFileTypes((prevFileTypes) => ({
                                ...prevFileTypes,
                                [patient.id]: 'image'
                            }));
                        } else {
                            setFileTypes((prevFileTypes) => ({
                                ...prevFileTypes,
                                [patient.id]: 'non-image'
                            }));
                        }
                    }
                });
        };

        checkImageType();
    }, [patients]);

    useEffect(() => {
        fetchPatients();
    }, []);
    return (
        <React.Fragment>
            {visible && <ShowSelectedPatient patient={selectedPatient} handleClose={closePopup} />}
            {showDeleteModal && <ConfirmDeleteModal patient={selectedPatient} handleClose={closeDeleteModal} />}
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <Card>
                        <Grid container direction="row" justifyContent="space-between" alignItems={'center'}>
                            <Grid item>
                                <CardHeader title="Registered Patients" />
                            </Grid>
                            <Grid item style={{marginRight: '25px'}}>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-profile"
                                    value={value}
                                    onChange={handleChange}
                                    placeholder="Search patients"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1.3rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                    aria-describedby="search-helper-text"
                                    inputProps={{
                                        'aria-label': 'weight'
                                    }}
                                    labelWidth={0}
                                />
                            </Grid>
                        </Grid>
                        <Divider />
                        <TableContainer>
                            <Table className={classes.table} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>#</TableCell>
                                        <TableCell>Patient name</TableCell>
                                        <TableCell align="left">ID/Iqama number</TableCell>
                                        <TableCell align="left" style={{whiteSpace: 'nowrap'}}>
                                            Date of birth
                                        </TableCell>
                                        <TableCell align="left">Mobile number</TableCell>
                                        <TableCell align="left">E-mail</TableCell>
                                        <TableCell align="left">Nationality</TableCell>
                                        <TableCell align="left">Registered date</TableCell>
                                        <TableCell align="left">Last login</TableCell>
                                        <TableCell align="left"></TableCell>
                                        <TableCell align="left"></TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {patients &&
                                        patients
                                            ?.filter((user) => !user.is_doctor)
                                            .map((patient, index) => (
                                                <TableRow key={patient.id}>
                                                    <TableCell align="left">{index + 1}</TableCell>
                                                    <TableCell component="th">{patient.full_name}</TableCell>
                                                    <TableCell align="left">{patient.iqama_number}</TableCell>

                                                    <TableCell align="left">{patient.date_of_birth}</TableCell>
                                                    <TableCell align="left">{patient.mobile_number}</TableCell>
                                                    <TableCell align="left">{patient.email}</TableCell>
                                                    <TableCell align="left">{patient.nationality}</TableCell>
                                                    <TableCell align="left">{formatDate(patient.created_at)}</TableCell>
                                                    <TableCell align="left">{formatDate(patient.last_login)}</TableCell>
                                                    <TableCell align="left" onClick={() => showSelectedPatient(patient)}>
                                                        <img src="images/eye.png" id="delete-icon" />
                                                    </TableCell>
                                                    <TableCell align="left" onClick={() => showDeletePopUpModal(patient)}>
                                                        <img src="images/trash-bin.png" id="delete-icon" />
                                                    </TableCell>
                                                </TableRow>
                                            ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Card>
                </Grid>
            </Grid>
        </React.Fragment>
    );
}
