import React, {useEffect, useState} from 'react';
import {Card, CardHeader, Divider, CardContent, Grid, Button} from '@material-ui/core';
import {gridSpacing} from '../../store/constant';
import {Container, ListGroup} from 'react-bootstrap';
import SubCard from '../../ui-component/cards/SubCard';
import axios from 'axios';
import {ShowSelectedConsultation} from './ShowSelectedConsultation';

export const ShowConsultations = (props) => {
    const BASE_URL = 'https://backend-test.alsahaba.sa';
    const [consultations, setConsultations] = useState([]);
    const [patients, setPatients] = useState([]);
    const [selectedConsultation, setSelectedConsultation] = useState(null);
    const [newConsultations, setNewConsultations] = useState(false);
    const [visibleSelectedConsultation, setVisibleSelectedConsultation] = useState(false);
    const [isLoading, setIsLoading] = useState(false);

    const fetchConsultations = async () => {
        setIsLoading(true);
        await axios.get(`${BASE_URL}/consultations/`).then((response) => {
            setConsultations(response.data);
            setIsLoading(false);
            var i = 0;
            if (props?.patient?.is_doctor === false) {
                while (i < response.data.length) {
                    if (response.data[i].patient_id === props?.patient.id) {
                        setNewConsultations(true);
                        break;
                    } else {
                        setNewConsultations(false);
                    }
                    i++;
                }
            } else {
                while (i < response.data.length) {
                    if (response.data[i].doctor_id === props?.patient.id) {
                        setNewConsultations(true);
                        break;
                    } else {
                        setNewConsultations(false);
                    }
                    i++;
                }
            }
        });
    };
    const fetchDoctorsOrPatients = async () => {
        await axios.get(`${BASE_URL}/users/`).then((response) => {
            setPatients(response.data);
        });
    };
    const showSelectedConsultation = (consultation) => {
        setSelectedConsultation(consultation);
        setVisibleSelectedConsultation(true);
    };
    const hideShowSelectedConsultations = () => {
        setVisibleSelectedConsultation(false);
    };
    const displayImg = () => {
        var dImg = document.getElementById('image-viewer');
        dImg.style.display = 'block';
    };
    const hideImg = () => {
        var hImg = document.getElementById('image-viewer');
        hImg.style.display = 'none';
    };
    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    useEffect(() => {
        displayImg();
        fetchConsultations();
        fetchDoctorsOrPatients();
    }, []);
    return (
        <>
            {visibleSelectedConsultation && (
                <ShowSelectedConsultation
                    consultation={selectedConsultation}
                    handleCloseShowSelectedConsultation={hideShowSelectedConsultations}
                />
            )}

            <Container id="image-viewer" onClick={hideImg}>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Card className="popup-container">
                            <CardHeader title="Consultation" />
                            <Divider />
                            <CardContent className="popup-body" style={{top: '250px', overflowY: 'auto', height: '75vh'}}>
                                <Button id="closeBtn" onClick={props?.handleCloseConsultations}>
                                    X
                                </Button>
                                <Grid container spacing={gridSpacing}>
                                    <Grid item xs={12} sm={6}>
                                        <SubCard title={props?.patient.full_name}>
                                            {!newConsultations ? (
                                                <h5 className="ml-4">No consultations yet!</h5>
                                            ) : (
                                                <>
                                                    {consultations &&
                                                        consultations
                                                            ?.filter((consultation) =>
                                                                props?.patient?.is_doctor
                                                                    ? consultation.doctor_id === props?.patient?.id
                                                                    : consultation.patient_id === props?.patient?.id
                                                            )
                                                            .map((filteredConsultation, index) => (
                                                                <ListGroup as="ol" key={filteredConsultation.id} className="borderBottom">
                                                                    {patients
                                                                        ?.filter((patient) =>
                                                                            props?.patient?.is_doctor
                                                                                ? filteredConsultation.patient_id === patient.id
                                                                                : filteredConsultation.doctor_id === patient.id
                                                                        )
                                                                        .map((filteredDoctor) => (
                                                                            <ListGroup.Item
                                                                                as="li"
                                                                                className="d-flex justify-content-between align-items-center"
                                                                                style={{cursor: 'pointer', marginLeft: '-25px'}}
                                                                                key={filteredDoctor.id}
                                                                                onClick={() =>
                                                                                    showSelectedConsultation(filteredConsultation)
                                                                                }
                                                                            >
                                                                                {index + 1}.
                                                                                <div className="ms-2 me-auto">
                                                                                    <div className="fw-bold">
                                                                                        {filteredConsultation.chief_complaint}
                                                                                    </div>
                                                                                    <strong style={{color: 'gray'}}>
                                                                                        {props?.patient?.is_doctor ? (
                                                                                            <p>
                                                                                                {' '}
                                                                                                Patient ID/Iqama: &nbsp;
                                                                                                {filteredDoctor.iqama_number}
                                                                                            </p>
                                                                                        ) : (
                                                                                            <p>
                                                                                                {' '}
                                                                                                Dr. ID/Iqama: &nbsp;
                                                                                                {filteredDoctor.iqama_number}
                                                                                            </p>
                                                                                        )}
                                                                                    </strong>
                                                                                    <br />
                                                                                    <strong>{filteredDoctor.full_name}</strong>
                                                                                </div>
                                                                                <strong>
                                                                                    {formatDate(filteredConsultation.created_at)}
                                                                                </strong>
                                                                            </ListGroup.Item>
                                                                        ))}
                                                                </ListGroup>
                                                            ))}
                                                </>
                                            )}
                                        </SubCard>
                                    </Grid>
                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </Container>
        </>
    );
};
