import React, {useEffect, useState} from 'react';
import {useLocation} from 'react-router-dom';
import {Card, CardHeader, Divider, CardContent, Grid, Button} from '@material-ui/core';
import {gridSpacing} from '../../store/constant';
import {Container} from 'react-bootstrap';
import SubCard from '../../ui-component/cards/SubCard';
export const ShowSelectedConsultation = (props) => {
    const hideShowModifyConsultation = () => {
        props?.handleCloseShowSelectedConsultation();
    };

    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    const displayImg = () => {
        var dImg = document.getElementById('image-viewer2');
        dImg.style.display = 'block';
    };
    const hideImg = () => {
        var hImg = document.getElementById('image-viewer2');
        hImg.style.display = 'none';
    };
    useEffect(() => {
        displayImg();
    }, []);

    return (
        <Container id="image-viewer2">
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <Card className="popup-container">
                        <CardHeader title="Consultation" />
                        <Divider />
                        <CardContent className="popup-body" style={{top: '250px', overflowY: 'auto', height: '75vh'}}>
                            <Button id="closeBtn" onClick={hideShowModifyConsultation}>
                                X
                            </Button>
                            <Grid container spacing={gridSpacing}>
                                <Grid item xs={12} sm={6}>
                                    <SubCard title="Consultation">
                                        <div className="mt-4">
                                            <h5>Chief Complaint:</h5>
                                            <strong>{props?.consultation.chief_complaint}</strong>
                                        </div>
                                        <div className="mt-4">
                                            <h5>History of Illness:</h5>
                                            <strong>{props?.consultation.history_of_illness}</strong>
                                        </div>
                                        <div className="mt-4">
                                            <h5>Review of systems:</h5>
                                            <strong>{props?.consultation.review_of_systems}</strong>
                                        </div>
                                        <div className="mt-4">
                                            <h5>Examinations:</h5>
                                            <strong>{props?.consultation.examination}</strong>
                                        </div>
                                        <div className="mt-4">
                                            <h5>Assessments/Plans:</h5>
                                            <strong>{props?.consultation.assessment}</strong>
                                        </div>
                                        <div className="mt-4">
                                            <h5>Medication:</h5>
                                            <strong>{props?.consultation.medication}</strong>
                                        </div>
                                        <div className="mt-4">
                                            <h5>Order labs / Home Medication / Image / Others::</h5>
                                            <strong>{props?.consultation.order_labs ? props?.consultation.order_labs : 'none'}</strong>
                                        </div>
                                        <div className="mt-4">
                                            <h5>Order labs / Home Medication / Image / Others: uploads:</h5>
                                            {props?.consultation.upload_order_labs ? (
                                                <a href={props?.consultation.upload_order_labs}>View file/image</a>
                                            ) : (
                                                'none'
                                            )}
                                        </div>
                                        <div className="mt-4">
                                            <h5>Sick leave:</h5>
                                            <strong>{props?.consultation.sick_leave}</strong>
                                        </div>
                                        <div className="mt-4 mb-4">
                                            <h5>Created At:</h5>
                                            <strong>{formatDate(props?.consultation.created_at)}</strong>
                                        </div>
                                    </SubCard>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </Container>
    );
};
